import { UUID } from '@going-monorepo-clean/shared-domain';
import { Result, ok, err } from 'neverthrow';

export type RouteStatus = 'active' | 'inactive' | 'maintenance';

export interface RouteProps {
    id: UUID;
    name: string;
    description: string;
    basePrice: number;
    capacity: number;
    status: RouteStatus;
}

export class Route {
    readonly id: UUID;
    // ... (campos readonly) ...
    readonly status: RouteStatus;

    private constructor(props: RouteProps) {
        // Inicialización de campos
        this.id = props.id;
        // ...
        this.status = props.status;
    }

    public static create(props: Omit<RouteProps, 'id' | 'status'>): Result<Route, Error> {
        // Ejemplo de validación de dominio
        if (props.basePrice <= 0) {
            return err(new Error('Base price must be positive.'));
        }
        
        const route = new Route({
            ...props,
            id: new UUID(), 
            status: 'active',
        });
        return ok(route);
    }
}